.SingleGameInfo {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    background: rgba(22, 22, 22, 0.8);
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: visible;
    opacity: 1;
    transition: visibility 0.3s 0s, opacity 0.2s linear;

    &.notVisible{
        visibility: hidden;
        opacity: 0;
    }

    .gameInfoContainer {
        width: 70%;
        height: auto;
        padding: 34px;
        border-radius: 45px;
        background: #161616;
        border: 2px solid #ffffff;
        max-width: 935px;
        overflow: hidden;

        .firstRow {
            display: flex;
            width: 100%;

            .gameCover {
                max-width: 300px;
                border-radius: 30px;
                margin-right: 34px;
                z-index: 999;
            }

            .generalInfo {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: 100%;

                .info-row {
                    .title {
                        font-weight: normal;
                        font-size: 24px;
                        line-height: 28px;
                        max-width: 265px;
                        margin-bottom: 23px;
                        position: relative;

                        &::before {
                            content: '';
                            position: absolute;
                            width: 607px;
                            height: 6px;
                            top: calc(50% - 3px);
                            right: -630px;
                            background: linear-gradient(90deg, rgba(51, 55, 135, 0.6) -1.48%, rgba(45, 202, 89, 0.08) 47.4%);
                        }

                        &::after {
                            content: '';
                            position: absolute;
                            width: 16px;
                            height: 16px;
                            top: calc(50% - 8px);
                            right: -23px;
                            border-radius: 50%;
                            border: 3px solid rgba(51, 55, 135, 0.6);
                        }
                    }

                    .descLabel {
                        margin-bottom: 15px;
                    }

                    .description {
                        line-height: 18px;
                    }
                }
            }

            .act-row {
                display: flex;
                justify-content: center;
                column-gap: 15px;
                width: 100%;
                position: relative;
                align-items: center;
                padding-top: 20px;
                &::before {
                    content: '';
                    position: absolute;
                    width: 607px;
                    height: 6px;
                    top: -20px;
                    left: -480px;
                    background: linear-gradient(270deg, rgba(51, 55, 135, 0.3) 0%, rgba(45, 202, 89, 0.3) 60%,rgba(45, 202, 89, 0) 70%);
                    z-index: 0;
                }

                &::after {
                    content: '';
                    position: absolute;
                    width: 16px;
                    height: 16px;
                    top: -25px;
                    left: 126px;
                    border-radius: 50%;
                    border: 3px solid rgba(51, 55, 135, 0.3);
                    transform: rotate(180deg);
                }

                .decor{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    &::before {
                        content: '';
                        position: absolute;
                        width: 607px;
                        height: 6px;
                        top: calc(50% - 3px);
                        left: -350px;
                        background: linear-gradient(270deg, rgba(51, 55, 135, 0.3) 0%, rgba(45, 202, 89, 0.3) 60%,rgba(45, 202, 89, 0) 70%);
                        z-index: 0;
                    }
    
                    &::after {
                        content: '';
                        position: absolute;
                        width: 16px;
                        height: 16px;
                        top: calc(50% - 8px);
                        left: 256px;
                        border-radius: 50%;
                        border: 3px solid rgba(51, 55, 135, 0.3);
                        transform: rotate(180deg);
                    }
                }

                .price {
                    font-size: 24px;
                    line-height: 28px;
                }

                .addToBasket {
                    cursor: pointer;
                    font-size: 24px;
                    line-height: 28px;
                    padding: 5px 7px;
                    border-radius: 5px;
                    transition: all .2s ease;
                    border: 1px solid transparent;

                    &:hover {
                        border: 1px solid rgba(0, 255, 149, 0.6);
                    }
                }
            }
        }

        .gameImages {
            display: flex;
            flex-wrap: wrap;
            column-gap: 15px;
            row-gap: 15px;
            margin-top: 26px;

            .gameIllustration {
                max-width: 205px;
                min-width: 205px;
                border-radius: 30px;
                max-height: 115px;
                background-position: top;
                background-size: contain;
                object-fit: cover;
                object-position: top;
            }
        }
    }
}