.MuiTab-root.Mui-selected{
    .MuiTypography-root{
        position: relative;
        &::after{
            content: '';
            position: absolute;
            width: 17px;
            height: 17px;
            border-radius: 50%;
            background: transparent;
            border: 4px solid #0E5A90;
            right: -30px;
            top: calc(50% - 8px);
       }
        &::before{
            content: '';
            position: absolute;
            width: 600px;
            height: 7px;
            right: -628px;
            top: calc(50% - 3px);
            background: linear-gradient(0deg, #0E5A90 5%, rgba(65, 234, 62, 0.575) 20%, rgba(65, 234, 62, 0) 25%);
            background: -webkit-linear-gradient(0deg, #0E5A90 5%, rgba(65, 234, 62, 0.575) 20%, rgba(65, 234, 62, 0) 25%);
            z-index: 1;
       }
   }
}
.MuiTypography-root{
    position: relative;
    //width: fit-content;
}
.NavigationPanelWrapper{
    min-width: 245px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    min-height: calc(100vh - 90px);
    background: #161616;
    .MuiButtonBase-root{
        padding: 35px 30px 35px 35px;
        height: auto;
        .MuiTab-wrapper{
            text-align: left;
            text-transform: capitalize;
            align-items: flex-start;
            p {
                letter-spacing: 0.06em;
           }
       }
   }
    .bottomContainer{
        padding: 35px 30px 35px 35px;
        width: 100%;
        position: relative;
        .notVisible{
            display: none !important;
        }
        &::before{
            content: '';
            position: absolute;
            width: 17px;
            height: 17px;
            border-radius: 50%;
            border: 4px solid rgba(51, 55, 135, 0.4);
            top: -159px;
            left: 112px;
       }
        &::after{
            content: '';
            position: absolute;
            height: 167px;
            width: 6px;
            top: -142px;
            left: 118px;
            background: linear-gradient(180deg, rgba(51, 55, 135, 0.4) 0%, rgba(45, 202, 89, 0.233) 40%, rgba(45, 202, 89, 0) 90%, rgba(45, 202, 89, 0) 100%);
       }
       
        .contactUsContainer{
            display: flex;
            align-items: center;
            padding-top: 50px;
            .icon{
                width: 30px;
                margin-left: 10px;
           }
       }
   }
    .myIndicator{
        display: none;
   }
}
